<template>
  <div class="wrapper">
    <div class="item">
      <Header :img="'alex'" :name="'Александр'" :status="'Онлайн'" />
      <Body :messeges="messeges" :id="1"/>
      <Footer :id="1" @send="send" />
    </div>

    <div class="item">
      <Header :img="'jane'" :name="'Евгений'" :status="'Онлайн'" />
      <Body :messeges="messeges" :id="2"/>
      <Footer :id="2" @send="send"/>
    </div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import Body from "@/components/Body.vue";
import Footer from "@/components/Footer.vue";

export default {
  components: {
    Header,
    Body,
    Footer,
  },
  data() {
    return {
      messeges: []
    };
  },
  mounted(){
    const localMess = localStorage.messeges
  
    this.messeges = localMess ? JSON.parse(localMess) : []
  },

  methods: {
    send(mess) {
      this.messeges.push(mess)
      localStorage.messeges = JSON.stringify(this.messeges)
    },
  },
};
</script>

