<template>
  <footer class="footer">
    <textarea
      placeholder="Написать сообщение..."
      type="text"
      class="footer__input"
      rows="1"
      v-model="message"
      @input="inputHeight"
    />
    <button class="footer__btn" v-if="message == ''" @click="closeModal = true">
      <img src="@/assets/img/photo.svg" alt="" />
    </button>
    <button class="footer__btn" v-else @click="send">
      <img src="@/assets/img/send.svg" alt="" />
    </button>

    <Transition name="modal">
      <div class="modal" @click="closeModal = false" v-if="closeModal == true">
        <div class="modal__block" @click.stop="">
          <h2 class="modal__block-title">
            Добавить фото
          </h2>
          <div class="modal__block-inputs">
            <label for="inp">
              <span>URL</span>
              <input type="text" id="inp" v-model="photo"/>
            </label>
            <label for="area">
              <span>Comment</span>
              <textarea id="area" v-model="comment"></textarea>
            </label>
          </div>
          <div class="modal__block-btns">
            <button class="modal__block-btns-btn del" @click="closeModal = false">
              Отмена
            </button>
            <button
              class="modal__block-btns-btn edit"
              @click="sendPhoto"
            >
              Добавить
            </button>
          </div>
        </div>
      </div>
    </Transition>
  </footer>
</template>

<script>
export default {
  props: ["id"],
  data() {
    return {
      message: "",
      closeModal: false,
      photo: '',
      comment: ''
    };
  },
  methods: {
    inputHeight() {
      const area = document.querySelectorAll(".footer__input");
      area.forEach((el) => {
        el.style.height = "auto";
        el.style.height = `${el.scrollHeight}px`;
      });
    },
    send() {
      if (this.message.trim().length > 0) {
        const mess = {
          id: this.id,
          text: this.message,
          time: `${new Date().getHours()}:${new Date().getMinutes()}`,
        };
        this.$emit("send", mess);
        this.message = "";
      }
    },
    sendPhoto(){
      if (this.photo.trim().length > 0) {
        const mess = {
          id: this.id,
          text: this.comment,
          photo: this.photo,
          time: `${new Date().getHours()}:${new Date().getMinutes()}`,
        };
        this.$emit("send", mess);
        this.photo = this.comment = this.closeModal = '';
        
      }
    }
  },
};
</script>

<style lang="scss" scoped>

.modal-enter-active,
.modal-leave-active {
  transition: all 0.3s ease;
}

.modal-enter-from,
.modal-leave-to {
  opacity: 0;
  transform: scale(1.5);
}
</style>